.text {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  h2 {
    margin-bottom: 15px;
    font-size: 24px;
    font-weight: 600;
    line-height: 33px;
  }

  p {
    font-size: 14px;
    font-weight: 300;
    line-height: 27px;
    letter-spacing: 0.02em;
  }

  @media (max-width: 768px) {
    margin-bottom: 30px;

    h2 {
      font-size: 30px;
    }

    p {
      font-size: 17px;
    }
  }
}
